<template>
    <div>
        <videoPlayer 
        class="video-player-box"
        ref="videoPlayer"
        :options="playerOptions"
        :playsinline="true"
        ></videoPlayer>
    </div>
</template>
<script>
import 'video.js/dist/video-js.css';
import { videoPlayer } from 'vue-video-player';
import RecordRTC from 'recordrtc';

export default {
    name:'tape',
    props:{
        //
        playerOptions:{
            type:Object,
            required:true
        },

        //
        videoFileName:{
            type:String,
            required:false,
            default:''
        }
    },
    data(){
        return{
            recorder:'',//
        }
    },
    created(){

    },
    methods:{

        //
        screenshot() {

            //video
            let video = this.$refs.videoPlayer;
            let videoEl = video.player.tech({
                IWillNotUseThisInPlugins: true
            }).el();

            //canvas
            let canvasEl = document.createElement('canvas');
            canvasEl.width = videoEl.videoWidth;
            canvasEl.height = videoEl.videoHeight;

            //
            let ctx = canvasEl.getContext('2d');
            ctx.drawImage(videoEl, 0, 0, 544, 960, 0, 0, 544, 960);

            //(base64)
            let image = canvasEl.toDataURL('image/png');

            return image;

        },

        //
        startCapture() {

            //video
            let video = this.$refs.videoPlayer;
            let videoEl = video.player.tech({
                IWillNotUseThisInPlugins: true
            }).el();

            //
            let stream = videoEl.captureStream();

            //recordRTC
            this.recorder = RecordRTC(stream, {
                type: 'video'
            });

            //
            this.recorder.startRecording();
        },

        //
        stopCapture() {

            //
            this.recorder.stopRecording(() => {

                //blob
                let blob = this.recorder.getBlob();
                this.videoFile = this.recorder.getBlob();

                //blobvideourl
                let url = URL.createObjectURL(blob);

                //
                this.openForm = true;
                this.videoOrImg = true;
                this.videoUrl = url;

                //blob
                let fileName = this.videoFileName+".webm";
                let fileObject = new File([blob], fileName, {
                    type: 'video/webm'
                });

                //
                return fileObject;
                
            });
        },
    },
    components:{
        videoPlayer
    }
}
</script>


